/* slider res */
.slider-container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 2rem;
}

.slider-image {
  /* width: 100%; */
  height: 100%;
  overflow: hidden;
}

.slider-image img {
  /* width: 100%; */
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}

.slider-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 16px;
  border: none;
  cursor: pointer;
  font-size: 18px;
  border-radius: 50%;
  z-index: 1;
  transition: background 0.3s ease;
}

.slider-button:hover {
  background: rgba(0, 0, 0, 0.8);
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

.slider-dots {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: background 0.3s ease;
}

.dot.active {
  background: white;
}

